@import "../../node_modules/@sparkpost/design-tokens/tokens.scss";

div#adminmenuwrap {
  ul#adminmenu {
    // Hide menu head.
    li#toplevel_page_wpmailpro {
      ul.wp-submenu {
        li.wp-first-item {
          display: none;
        }
      }
    }

    // Fix the Collapse menu item being bigger for some weird reason.
    li#collapse-menu {
      font-size: 0.8rem;
      line-height: 2;

      button {
        line-height: 1.5;
      }
    }
  }
}

#wpmp-admin-page-container {
  padding-bottom: 3rem;

  div.notice.license-activation,
  div.notice.sending-limit-flag {
    display: none;
  }

  div.admin-page-core-content {
    position: relative;
    //max-width: 1000px;

    // Disable the ".wp-core-ui select" base styles.
    select {
      min-height: auto;
      max-width: 100%;
      background: #fff;
      background-size: auto;
    }

    nav.plugin-tabs {
      button {
        &:focus:after {
          display: none;
        }

        &[aria-selected="true"] {
          background: rgba(#111, 0.025);
          border-radius: 4px 4px 0 0;
        }
      }
    }

    div.tab-content {
      position: relative;

      *:focus,
      *:focus-within {
        outline: none;

        &:after {
          display: none;
          box-shadow: none;
        }
      }
    }

    // Loader.
    div.loader {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: rgba(#fff, 0.25);
      display: flex;
      align-items: center;
      justify-content: center;
      backdrop-filter: saturate(90%) blur(3px);
    }
  }

  p.help-link {
    margin-top: 1rem;
    font-size: 0.8rem;
    text-align: right;

    a {
      text-decoration: none;
      color: rgba(#111, 0.5);
      text-shadow: 0px 1px 0px #fff;

      &:hover {
        color: color(blue, 700);
      }
    }
  }

  p.free-license-callout {
    margin-top: 1rem;
    font-size: 1rem;
    //text-align: right;

    a {
      text-decoration: none;
      color: rgba(#111, 0.5);
      text-shadow: 0px 1px 0px #fff;

      &:hover {
        color: color(blue, 700);
      }
    }
  }

  p.field-desc {
    font-size: 0.75rem;
    line-height: 1.3;
    margin-top: 0.5em;
  }

  // Header.
  header.settings-page-header {
    position: relative;

    h1.wp-heading-inline {
      margin-bottom: 0.5em;
    }

    div.feedback-notice {
      position: absolute;
      top: 0;
      right: 0;
      z-index: 5000;
      box-shadow: 0px 5px 15px -5px rgba(#000, 0.25);
    }
  }

  /**
   * Utility classes.
   */
  .vertical-align {
    align-items: center;
    display: flex;
  }

  .restricted-width {
    max-width: 30rem;

    &.xl {
      max-width: 45rem;
    }
  }

  .align-bottom {
    display: flex;
    align-items: flex-end;
  }

  .dkim-record-value {
    padding-left: 1em;
  }

  code {
    word-break: break-all;
  }

  button[disabled] {
    opacity: 0.325;
  }

  p.empty-table-message {
    text-align: center;
    font-style: italic;
  }

  span.copy-link {
    color: color(blue, 700);
    font-size: 85%;
    display: inline-block;
    margin-left: 0.5em;
    cursor: pointer;

    &:hover {
      text-decoration: underline;
    }
  }

  /**
   * Panels.
   */
  div.tab-section {
    margin-top: 1rem;
    background: rgba(#fff, 0.8);

    &.section-verify-domain {
      p.last-verification-status {
        &.valid {
          color: color(green, 700);
        }

        &.invalid {
          color: color(red, 700);
        }

        span.icon {
          margin-right: 0.5em;
        }

        span.text {
          font-weight: bold;

          small {
            font-size: 80%;
            font-weight: normal;
          }
        }
      }
    }

    &.section-metrics {
      > div:first-child {
        padding: 0.75rem 1.25rem;
      }

      header {
        display: flex;
        flex-direction: row;
        align-items: center;
        //margin-bottom: 1rem;

        h2 {
          font-size: 1.25rem;
          line-height: 1.75rem;
          font-weight: 600;
        }

        button {
          margin-left: auto;
          width: 1em;
        }
      }

      ul.filters-button {
        margin: 0;
        padding: 0;
        width: 75%;
        align-items: flex-start;

        li {
          display: inline-block;
          margin: 0;
          padding: 0.375em 1em;
          border-radius: 2em;
          font-size: 0.925rem;
          cursor: pointer;

          &.active {
            background: color(gray, 200);
            font-weight: bold;
            border-radius: 2em;
          }

          &:hover:not(.active) {
            background: #fafafa;
          }

          + li {
            margin-left: 0.5em;
          }
        }
      }

      div.metrics-box {
        text-align: center;
        padding: 1.5rem 0.5rem;

        .icon {
          margin-bottom: 0.25rem;
          fill: color(teal, 600);
        }

        p {
          font-size: 0.9rem;
          text-transform: uppercase;
          font-weight: 300;

          strong {
            //
          }
        }
      }
    }
  }

  div.metrics-charts-container {
    //margin-top: 1rem;

    .first-col {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }

    div.metrics-box {
      padding: 0.5rem 0;

      .icon-col {
        display: flex;
        align-items: center;

        .icon {
          fill: color(blue, 700);
        }
      }

      .content-col {
        h3 {
          font-size: 1.125rem;
          margin: 0;
          line-height: 1.1;
        }

        p.stat {
          margin: 0;
        }
      }
    }

    div.pie-and-title {
      position: relative;

      header.title {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        display: flex;
        align-items: center;
        text-align: center;

        h6 {
          width: 100%;
          font-size: 0.8rem;
          font-weight: normal;
          line-height: 1.3;
          margin: 0;
          //color: color(gray, 500);

          strong {
            display: block;
            color: color(blue, 700);
            font-size: 1.75em;
          }
        }
      }
    }

    .engagement-box-container {
      flex: 1;
      height: 100%;
      margin-top: 0;

      h2 {
        font-size: 1.125rem;
        margin: 0;
        line-height: 1.1;
      }

      div.engagement-box {
        padding-top: 1rem;

        p.stat {
          color: color(blue, 700);
          font-weight: bold;
          font-size: 1.6rem;
        }

        p.caption {
          font-size: 1rem;

          small {
            color: color(gray, 500);
          }
        }
      }

      hr + div.engagement-box {
        padding-top: 0;
      }
    }
  }

  div.tab-section.section-sending-limit {
    div.section-inner {
      display: flex;
      flex-direction: row;
      align-items: center;

      > h3 {
        font-size: 1.125rem;
        margin: 0;
        line-height: 1.1;
        margin: 0 1em;
      }

      p.numbers {
        padding: 0.5em 1em;
        line-height: 1;
        background-color: color(gray, 300);
        border-radius: 0.5rem;
        font-size: 0.85rem;
        color: #333;
        font-weight: bold;
        text-shadow: 0px 1px 0px rgba(#fff, 0.5);
      }

      div.progress-bar {
        border-radius: 0.5rem;
        flex: 1;
        background-color: color(gray, 300);
        height: 1.7rem;
        margin: 0 1em;
        padding: 0.2rem;

        div.progresser {
          border-radius: 0.4rem;
          height: 1.3rem;
          background-size: 41.54px 31.3px;
          $light_c: color(blue, 600);
          $dark_c: darken($light_c, 5%);
          background-color: $light_c;
          background-image: linear-gradient(
            143deg,
            $dark_c 30%,
            $light_c 30%,
            $light_c 50%,
            $dark_c 50%,
            $dark_c 80%,
            $light_c 80%,
            $light_c 100%
          );
        }

        &.almost div.progresser {
          $light_c: #e9672b;
          $dark_c: darken($light_c, 6%);
          background-color: $light_c;
          background-image: linear-gradient(
            143deg,
            $dark_c 30%,
            $light_c 30%,
            $light_c 50%,
            $dark_c 50%,
            $dark_c 80%,
            $light_c 80%,
            $light_c 100%
          );
        }

        &.full div.progresser {
          $light_c: color(red, 600);
          $dark_c: darken($light_c, 10%);
          background-color: $light_c;
          background-image: linear-gradient(
            143deg,
            $dark_c 30%,
            $light_c 30%,
            $light_c 50%,
            $dark_c 50%,
            $dark_c 80%,
            $light_c 80%,
            $light_c 100%
          );
        }

        &.overflow div.progresser {
          $light_c: color(yellow, 400);
          $dark_c: darken($light_c, 2.5%);
          background-color: $light_c;
          background-image: linear-gradient(
            143deg,
            $dark_c 30%,
            $light_c 30%,
            $light_c 50%,
            $dark_c 50%,
            $dark_c 80%,
            $light_c 80%,
            $light_c 100%
          );
        }
      }

      a.upgrade-button {
        font-weight: bold;
        color: #fff;
        text-decoration: none;
        background: color(blue, 700);
        font-size: 0.9rem;
        padding: 0.25em 1.5em;
        border-radius: 1em;

        &:hover {
          background: color(gray, 700);
          color: #fff;
        }
      }
    }
  }

  div.tab-section.email-sender-settings {
    select.sender-mail-suffix {
      padding-right: 3em;
    }
    div.force-sender-setting {
      margin-top: 1.5rem;

      span#force_sender_emailLabel {
        font-weight: bold;
      }
    }
  }

  div.section-logs {
    div.tab-section.date-range-section {
      > div:first-child {
        padding: 0.75rem 1.25rem;
      }
    }

    nav.logs-table-navigator {
      margin-top: 1rem;
      display: flex;
      flex-direction: row;
      align-items: center;

      p.total {
        margin: 0.5em auto 0 0;
        font-size: 0.9rem;
        color: rgba(#111, 0.6);
        text-shadow: 0px 1px 0px rgba(#fff, 0.5);
      }
    }

    nav.date-range-selectors {
      display: flex;
      flex-direction: row;

      ul.range-selector {
        width: 75%;
        margin: 0;
        padding: 0;

        li {
          margin: 0;
          padding: 0.375em 1em;
          border-radius: 2em;
          font-size: 0.925rem;
          cursor: pointer;
          display: inline-block;

          &.active {
            font-weight: bold;
            background: color(gray, 200);
          }

          &:not(.active) {
            cursor: pointer;
          }
        }
      }

      button.refresh-button {
        margin-left: auto;
      }
    }

    table.logs-table {
      table-layout: fixed;
      width: 100%;

      tbody {
        tr {
          td {
            font-size: 0.9rem;

            span.log-status {
              font-size: 0.6rem;
              font-weight: 500;
              display: block;
              width: 100%;
              background: #dadada;
              padding: 0.1em;
              text-align: center;
              border-radius: 2px;
              text-transform: uppercase;
              cursor: pointer;

              > span {
                display: block;
              }

              &.status-accepted,
              &.status-delivered {
                background: color(green, 700);
                color: #fff;
              }

              &.status-error {
                background: color(yellow, 300);
              }

              &.status-failed {
                background: color(red, 700);
                color: #fff;
              }
            }
          }
        }
      }

      th.col-date,
      td.col-date {
        width: 16%;
      }

      th.col-subject,
      td.col-subject {
        width: 22%;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }

      th.col-sender,
      td.col-sender {
        width: 24%;
      }

      th.col-recipient,
      td.col-recipient {
        width: 18%;
      }

      th.col-status,
      td.col-status {
        width: 15%;
      }

      th.col-actions,
      td.col-actions {
        width: 5%;
      }

      td.col-actions {
        text-align: center;
        padding-left: 0;
        padding-right: 0;

        svg {
          cursor: pointer;

          &:hover {
            fill: color(blue, 700);
          }
        }
      }

      tr.col-actions-row {
        background: #fff;
        box-shadow: 0px 12px 25px -25px rgba(0, 0, 50, 0.45) inset;
        border-bottom: 2px solid rgba(0, 0, 50, 0.05);

        &.gray {
          box-shadow: 0px 12px 25px -25px rgba(0, 0, 50, 0.6) inset;
          background: #f5f8fa;
        }

        div.log-row-data {
          display: flex;
          flex-direction: row;
          align-items: center;

          > div {
            h6 {
              font-size: 0.9rem;
            }

            p {
              font-size: 0.8rem;
            }

            + div {
              margin-left: 1rem;
              padding-left: 1rem;
              border-left: 1px solid rgba(#111, 0.1);
            }

            &.subject {
              // min-width: 25rem;
              max-width: 45%;
              //width: 40%;
            }

            &.engagement {
              span.icon {
                &.positive {
                  color: color(green, 700);
                }

                &.negative {
                  color: color(red, 700);
                }
              }
            }
          }

          > nav {
            margin-left: auto;

            a {
              $c: color(blue, 700);
              text-decoration: none;
              color: $c;
              border: 1px solid $c;
              border-radius: 2em;
              padding: 0.5em 1em;

              &:hover {
                color: #fff;
                background: $c;
              }
            }
          }
        }
      }

      tr.col-actions-fake-row {
        height: 0px !important;
        overflow: hidden !important;

        td {
          padding: 0 !important;
        }
      }
    }
  }
}

/**
 * Email preview page.
 */
body.admin_page_wpmailpro-preview-email {
  overflow: hidden;

  #adminmenumain,
  #wpadminbar,
  //#wpbody-content *,
  #wpfooter {
    display: none;
  }

  #wpbody-content #wpmp-email-content-preview,
  #wpbody-content #wpmp-email-content-preview div.email-preview-wrapper {
    display: block;
  }

  #wpcontent {
    padding: 0 !important;
    margin-left: 0 !important;
  }

  #wpbody-content #wpmp-email-content-preview div.email-preview-wrapper {
    display: block;
    height: 100vh;
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    margin-top: 2rem;
    //white-space: pre;
    //padding-top: 2rem;
  }

  #wpbody-content #wpmp-email-content-preview div.email-preview-wrapper body {
    //white-space: pre;
  }
}

html.wp-toolbar {
  #wpbody-content #wpmp-email-content-preview {
    margin-top: -32px !important;
  }
}
